// src/components/IntroSlider.tsx

"use client";
import React, { useLayoutEffect } from 'react';
import gsap from 'gsap';

// onAnimationComplete නම් function එකක් props වලින් ලබාගැනීම
const IntroSlider = ({ onAnimationComplete }: { onAnimationComplete: () => void }) => {

  useLayoutEffect(() => {
    const ctx = gsap.context(() => {
      // අකුරු වෙන වෙනම span වලට වෙන් කිරීම
      document.querySelectorAll(".split-text").forEach(el => {
        const text = el.textContent || "";
        if (el.children.length === 0) {
            el.innerHTML = "";
            text.split("").forEach(char => {
                const span = document.createElement("span");
                span.className = "letter inline-block";
                span.style.transformStyle = "preserve-3d";
                span.textContent = char === " " ? "\u00A0" : char;
                el.appendChild(span);
            });
        }
      });

      // GSAP Timeline එක
      const t1 = gsap.timeline({
        onComplete: onAnimationComplete // Animation එක අවසන් වූ පසු props වලින් ආ function එක call කිරීම
      });

      t1.from(".letter", {
        opacity: 0,
        rotationX: -120,
        filter: "blur(10px)",
        yPercent: 50,
        transformOrigin: "center 50% -50",
        stagger: { each: 0.03, from: "random" },
        duration: 0.8,
        ease: "power3.out",
        delay: 0.5
      })
      .to(".letter", {
        opacity: 0,
        yPercent: -50,
        filter: "blur(10px)",
        duration: 0.6,
        ease: "power3.in",
        stagger: { each: 0.02, from: "center" }
      }, "+=1.2")
      .to("#intro-slider-container", { // සම්පූර්ණ slider එක fade out කිරීම
        opacity: 0,
        duration: 0.5
      });
    });

    return () => ctx.revert();
  }, []); // [] නිසා මෙම effect එක එක් වරක් පමණක් ක්‍රියාත්මක වේ

  return (
    <div id="intro-slider-container" className="h-screen w-full fixed top-0 left-0 z-50 bg-black flex justify-center items-center">
        <div className="text-center">
            <h1 className="text-4xl sm:text-5xl md:text-8xl font-bold text-white p-4 split-text">FIRST EVER</h1>
            <h1 className="text-4xl sm:text-5xl md:text-8xl font-bold text-white p-4 split-text">MINISTRY RECOGNIZED</h1>
            <h1 className="text-4xl sm:text-5xl md:text-8xl font-bold text-white p-4 split-text">ICT DAY</h1>
        </div>
    </div>
  );
};

export default IntroSlider;